html, body {
    min-height: 100%;
    height: 100%;
    font: 14px "Century Gothic", Futura, sans-serif;
    background-color: rgb(32, 32, 32);
    color: rgb(201, 201, 201);
  }
.NumberLabel{
    float: left;
    text-align: left;
}
.NumberLabelText{
    vertical-align: center;
    padding:0;
    margin:0;
}
.CharacterPicker{
    font: 20px "Century Gothic", Futura, sans-serif;
}
.Stat{
    padding: 15px;
}
.FullLine{
    width:100%
}
.ConditionalLabel{
    padding-left: 15px;
    padding-right: 15px;
    height:100%;
}
ul {
    list-style-type: none;
    padding:0;
    margin:0;
}
.StatListItemLabel {
    padding: 0;
    margin: 0;
    text-align:left;
}
.StatListItem { background: rgba(31, 31, 31, 0.5); 
    padding: 0;
    margin: 0;
    text-align:left;

    font: 18px "Century Gothic", Futura, sans-serif;}
.StatListItem:nth-child(odd) { background: rgba(77, 77, 77, 0.5); }
.StatWrapper:after {
        clear: both;
        content: "";
        display: table;
    }
.StatName{
    display:inline-block;
    text-align:left;
    margin:0;
    padding:5px;
}
.StatValue{   
    float:right;
    text-align:right;
    margin:0;
    padding: 5px;
    padding-left:35px;
    min-width:40px;
}
.EntryBox{
    float: right;
    text-align: right;
    width: 60px;
    margin-left: 20px;
}
.StatPicker{
    float:right;
    text-align:right;
    margin-left: 10px;
}
.BuildInfo {
    text-align: center;
}
.title {
    font: 50px "Century Gothic", Futura, sans-serif;
    margin: none;
    padding: none;
}
.PrimarySection {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
}

.BoxInfo {
    border-style: solid;
    border-width: 3px;
    border-color: rgb(62, 69, 86);
    background-color: rgba(53, 61, 75); 
    border-radius: 8px;
    padding: 5px;
    margin: 15px;
}
.tab-content {
    border-style: solid;
    border-width: 3px;
    border-color: rgb(62, 69, 86);
    background-color: rgba(53, 61, 75); 
    border-radius: 8px;
    border-top-left-radius: 0px;
    padding: 5px;
    margin: 15px;
    padding-top:0;
    margin-top:0;
}
input, select, textarea{
    color: rgb(201, 201, 201);
    border-style: solid;
    border-width: 3px;
    border-color: rgb(43, 47, 58);
    background-color: rgb(74, 86, 105); 
  }
.GeneratedUrl{
    word-wrap: break-word;
    float:right;
    font: 10px "Century Gothic", Futura, sans-serif;
}
.SectionHeader {
    font: 60px "Century Gothic", Futura, sans-serif;
}
.subtitle {
    font: 30px "Century Gothic", Futura, sans-serif;
}
.subtitle2 {
    font: 20px "Century Gothic", Futura, sans-serif;
    padding-bottom:0px;
    margin-bottom:0px;
    padding-top:0px;
    margin-top:0px;
}
.subtitle3 {
    font: 10px "Century Gothic", Futura, sans-serif;
    padding:0px;
    padding-left:15px;
    margin:0px;
    text-align:left;
    float:left;
    width:100%;
}
.PlusButton{
    display:inline-block;
    margin-top:18px;
    padding:0px;
    padding-left:5px;
    padding-right:5px;
}
.ConditionalLayout {
    display: flex;
    flex-direction: column; 
    flex-wrap: nowrap;
    justify-content: center;
}
.formatLabel{
    font: 10px "Century Gothic", Futura, sans-serif;
    color:rgb(145, 145, 145);
}
.NoSpacing{
    padding:0;
    margin:0;
}
.UrlGenerator{
    max-width: 700px;
    min-width: 500px;
    
    text-align: left;
}
.InfoCollumn {
    width: 300px;
    float:left;
    padding: 10px;
}
.Loadout-body {
    padding: 20px;
    height:100%;
    max-width:960px;
    margin-left: auto;
    margin-right: auto;
    text-align: center;
    background-color: rgb(35, 35, 36); 
}
button{
    border-color:  rgb(74, 86, 105);
    background-color:rgb(43, 47, 58); 
    margin: 5px;
    padding: 5px;
    color: rgb(201, 201, 201);
}
.tab-list {
    text-align:left;
    padding-bottom:0;
    padding-left:15px;
    margin-bottom:0;
  }
  
  .tab-list-item {
    border: solid rgb(74, 86, 105);
    background-color:rgb(43, 47, 58); 
    border-width: 1px 1px 0 1px;
    
    border-top-left-radius: 8px;
    border-top-right-radius: 8px;
    display: inline-block;
    list-style: none;
    margin-bottom: -1px;
    padding: 0.5rem 0.75rem;
  }
  
  .tab-list-active {
    background-color: rgba(53, 61, 75); 
    border: solid rgb(74, 86, 105);
    border-width: 1px 1px 0 1px;
  }
  .green{
    color:green;
  }
  .red{
      color:red;
  }
  .grey{
      color:grey
  }